module.exports = {"01 simple":"import React from 'react';\nimport ReactDOM from 'react-dom';\n\nReactDOM.render(\n  <h1 id=\"title\" height={54}>\n    Hello world\n  </h1>,\n  document.querySelector('#app')\n);\n","02 fragment":"import React from 'react';\nimport ReactDOM from 'react-dom';\n\nReactDOM.render(\n  <>\n    <h2>The Damu-Madu Project</h2>\n    <p>\n      The Damu-Madu sister project aims to bring the developer experience of\n      writing JSX (XML in JavaScript) without having to use React.\n    </p>\n    <p>\n      JSX is independent of React, you don't have to use React just to use JSX\n      in your development process.\n    </p>\n    <p>\n      The Damu-Madu sister project contains solution for both client render and\n      server render:\n    </p>\n    <ul>\n      <li>\n        <a href=\"https://github.com/tanhauhau/madu\">madu</a> - Writing jsx as if\n        html string\n      </li>\n      <li>\n        <a href=\"https://github.com/tanhauhau/damu\">damu</a> - Converts jsx to\n        vanilla javascript\n      </li>\n    </ul>\n  </>,\n  document.querySelector('#app')\n);\n","03 list":"import React from 'react';\nimport ReactDOM from 'react-dom';\n\nReactDOM.render(\n  <div className=\"body\">\n    <h1 className=\"header\">You shall not pass</h1>\n    <ul>\n      <li id=\"frodo\">Frodo Baggins</li>\n      <li id=\"samwise\">Samwise Gamgee</li>\n      <li id=\"gandalf\">Gandalf the Grey</li>\n      <li id=\"legolas\">Legolas</li>\n      <li id=\"gimli\">Gimli</li>\n      <li id=\"aragorn\">Aragorn</li>\n      <li id=\"boromir\">Boromir</li>\n      <li id=\"meriadoc\">Meriadoc</li>\n      <li id=\"peregrin\">Peregrin</li>\n    </ul>\n  </div>,\n  document.querySelector('#app')\n);\n","04 map":"import React from 'react';\nimport ReactDOM from 'react-dom';\n\nconst buyers = [\n  {\n    firstName: 'James',\n    lastName: 'Steven',\n    followers: [{ firstName: 'James', lastName: 'Steven' }],\n  },\n  {\n    firstName: 'James',\n    lastName: 'Steven',\n    followers: [\n      { firstName: 'James', lastName: 'Steven' },\n      { firstName: 'James', lastName: 'Steven' },\n    ],\n  },\n  {\n    firstName: 'James',\n    lastName: 'Steven',\n    followers: [\n      { firstName: 'James', lastName: 'Steven' },\n      { firstName: 'James', lastName: 'Steven' },\n      { firstName: 'James', lastName: 'Steven' },\n    ],\n  },\n  {\n    firstName: 'James',\n    lastName: 'Steven',\n    followers: [\n      { firstName: 'James', lastName: 'Steven' },\n      { firstName: 'James', lastName: 'Steven' },\n      { firstName: 'James', lastName: 'Steven' },\n      { firstName: 'James', lastName: 'Steven' },\n    ],\n  },\n];\n\nReactDOM.render(\n  <ul>\n    {buyers.map(buyer => (\n      <li>\n        {buyer.firstName + ' - ' + buyer.lastName}\n        {buyer.followers.map((follower, index) => (\n          <>\n            <li>\n              {index + '. ' + follower.firstName + ' - ' + follower.lastName}\n            </li>\n            <li className=\"hidden\">{index % 2 == 0 ? 'odd' : 'even'}</li>\n          </>\n        ))}\n      </li>\n    ))}\n  </ul>,\n  document.querySelector('#app')\n);\n"}